<template>
  <div class="contact-us">
    <v-img
      :src="require('../assets/images/banner_image3.jpg')"
      aspect-ratio="5"
      gradient="to top, #FFE52D, rgba(255, 229, 45, 0.55)"
      class="d-flex align-center justify-center"
      style="padding: 5vmin 10vmin 5vmin 10vmin;"
    >
      <p :class="changeDescClasses + ' text-center'">
        At Change Consulting, we believe changemakers plus
        communications equals good. Read our blog for the latest
        tools, tips, and best practices on social change communications.
      </p>
    </v-img>
    <div class="testimonial" style="padding: 5vmin 10vmin 5vmin 10vmin;">
      <v-row no-gutters="" align="center" justify="space-between">
        <v-col cols="12" class="px-2">
          <v-text-field
            solo
            label="Search Posts..."
            prepend-inner-icon="fa-search"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row no-gutters="" align="center" justify="center">
        <v-col xl="8" lg="9" md="10" sm="11" cols="12">
          <masonry
            :cols="{default: cardCols}"
            :gutter="{default: masonGutter}"
            >
            <blog-card
              title="ACLU fights for LGBTQ students' free speech rights in lawsuit against school"
              description="A Description of What is Happening"
              :thumbnail="require('../assets/images/serving/3rd_st.jpg')"
              clientSite="blog-post"
              author="William Ramirez, The Fresno Bee"
              :datePosted="Date.now()"
              v-for="i in 6"
              :key="i"
            ></blog-card>
          </masonry>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import BlogCard from '../components/BlogCard.vue';

export default {
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    bannerClasses() {
      const baseClasses = 'primary white--text display-2 text-center banner-text font-weight-black';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} ${this.titleSize} pa-0`;
        case 'sm':
          return `${baseClasses} ${this.titleSize} pa-2`;
        case 'md':
          return `${baseClasses} ${this.titleSize} pa-3 mb-8`;
        case 'lg':
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
        default:
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
      }
    },

    changeDescClasses() {
      const baseClasses = 'font-weight-bold grey--text text--darken-4 pa-0 ma-0';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} headline`;
        default:
          return `${baseClasses} headline`;
      }
    },

    changeSubDescPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'px-5 py-1';
        case 'sm':
          return 'px-7 py-2';
        case 'md':
          return 'px-9 py-3';
        case 'lg':
          return 'px-11 py-4';
        default:
          return 'px-11 py-4';
      }
    },

    quoteHeight() {
      return 350;
    },

    titlePadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'py-3';
        case 'sm':
          return 'py-5';
        case 'md':
          return 'py-7';
        case 'lg':
          return 'py-9';
        default:
          return 'py-9';
      }
    },

    masonGutter() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return '10px';
        case 'sm':
          return '15px';
        case 'md':
          return '20px';
        case 'lg':
          return '25px';
        default:
          return '30px';
      }
    },

    cardCols() {
      return this.$vuetify.breakpoint.xsAndDown ? 1 : 2;
    },
  },

  components: { BlogCard },
};
</script>
